import React, { Component } from 'react';
import {Segment} from 'semantic-ui-react';
import IndexPortal from './IndexPortal';
import CategoryPortal from './CategoryPortal';
import SortPortal from './SortPortal';
import Keypad from './Keypad';

class Sidebar extends Component {

    static View = Object.freeze ({ keypad: "keypad", letters: "letters", categories: "categories", sorted: "sorted", none: "none" });
    
    state = {view:this.props.view};

    setView = (view) => {
        this.setState({view:view});
    }

    preventTouchMove = (e) => {
        // console.log(e);
        e.preventDefault();
        e.stopPropagation();
      }

    componentWillReceiveProps(nextProps) {
        // console.log('Sidebar.componentWillReceiveProps', this.props);
        if (!nextProps.open) {
            this.setView(Sidebar.View.none);
        }
        if (nextProps.adding) {
            this.setView(Sidebar.View.categories);
        }
        else if (nextProps.item.name !== this.props.item.name) {
            if (nextProps.item.name.length === 0)
                this.setView(Sidebar.View.sorted);
            else
                this.setView(Sidebar.View.keypad);
        }

    }
    
    render() {
        
        const View = Sidebar.View;
        
        return (
        <div>
                {/* <Segment textAlign="center" inverted basic //onTouchMove={this.preventTouchMove.bind(this)} 
                    className="ui black"
                    style={{
                        zIndex: 5001,
                        backgroundColor: '#3A3A3A',
                        position: 'fixed',
                        top:"62px",
                        right: '0%', 
                        width:"50%",
                        height:"calc(100% - 80px)",
                        padding:'0px 0px',
                        borderStyle:'none',
                        borderRadius:0
                    }}
                    onClick={this.preventTouchMove}
                    onTouchMove={this.preventTouchMove}
                    onTouchStart={this.preventTouchMove}
                > */}
                    <IndexPortal {...this.props} open={this.props.view === View.letters}/>
                    <CategoryPortal {...this.props} open={this.props.view === View.categories}/>
                    {/* <SortPortal {...this.props} open={this.props.view === View.sorted}/> */}
                    <Keypad {...this.props} open={this.props.view === View.keypad} setView={this.setView}/>                
                {/* </Segment> */}
        </div>
        );
    }
}

export default Sidebar;



