const POSTURL = "https://api.jsonbin.io/b/5a9e1a52859c4e1c4d5de983";
const FETCHURL = "https://api.jsonbin.io/b/5a9e1a52859c4e1c4d5de983/latest";

var newInput=true;
var lastFilteredItems =[];

String.prototype.trim = function() {
  var trimmed = this.replace(/^\s+|\s+$/g, '');
  return trimmed;
};
String.prototype.ltrim = function() {
  var trimmed = this.replace(/^\s+/g, '');
  return trimmed;
};
String.prototype.rtrim = function() {
  var trimmed = this.replace(/\s+$/g, '');
  return trimmed;
};


function titleCase(str) {
  // console.log('titlecase',str.toLowerCase().split(' ').length);
  return str.trim().toLowerCase().split(' ').map(function(word) {
    return (word.length > 0) ? word.replace(word[0], word[0].toUpperCase()) : "";
  }).join(' ');
}
  
const strCompare = (s1,s2) => {
  const u1 = s1.toUpperCase();
  const u2 = s2.toUpperCase();
  if (u1 < u2)
    return -1;
  if (u1 > u2)
    return 1;
  return 0;
}

window.oncontextmenu = function(event) {
  event.preventDefault();
  event.stopPropagation();
  return false;
};

var lastX;
// document.ontouchmove = function(e) {
