import React, { Component } from 'react';
import {TransitionablePortal, Segment} from 'semantic-ui-react';

class SidebarView extends Component {

    shouldComponentUpdate = (nextProps) => {
        // console.log(nextProps.open !== this.props.open);
        return (nextProps.open !== this.props.open);
    }

    preventTouchMove = (e) => {
        // console.log('preventTouchMove',e, e.type);
        if (e.type === "touchmove") {
            // console.log("preventing touchmove");
            e.preventDefault();
        }
        // e.stopPropagation();
    }

    render() {
        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                >
                    <Segment inverted textAlign="center"
                        style={{
                            borderRadius:"0em",
                            position: 'fixed',
                            top: '60px',
                            right: '0%',
                            width:"50%",
                            height:"calc(100% - 80px)",
                            margin:0,
                            padding:10,
                            // paddingTop:10,
                            // paddingBottom:0,
                            zIndex: 5004
                        }}
                        onClick={this.preventTouchMove}
                        onTouchMove={this.preventTouchMove}
                        onTouchStart={this.preventTouchMove}
                    >
                        {this.props.children}
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default SidebarView;



