import React, {Component} from 'react';
import {Table, Container, Segment} from 'semantic-ui-react'
import color from '../colors';

let buttonPressTimer;

 

class Item extends Component {

    handleTouchStart = (e) => {
        buttonPressTimer = setTimeout(() => this.doTimeout() , 2000);
    }
    doTimeout = () => {
        this.props.deleteItem(this.props.item);
    }
    handleTouchEnd = () => {
        clearTimeout(buttonPressTimer);
    }       

    render() {
        const props = this.props;
        const selected = (props.item.name === props.selectedItem.name);
        return (
            <Table.Row 
                // className={selected?'itemRowSelected':null} 
                style={{backgroundColor: selected?'#FFFFFF':color(props.item.tags), fontWeight:selected?'bolder':null}} 
                onClick={props.rowClickEvent} 
                onTouchStart={this.handleTouchStart.bind(this)}
                onTouchEnd={this.handleTouchEnd.bind(this)}
                onTouchMove={this.handleTouchEnd.bind(this)}
            >
                <Table.Cell textAlign="center" width={4}    
                    style={{padding:0, lineHeight:1}}
                >
                    {/* {(props.item.qty.length>0)?props.item.qty:"--"} <br/> {props.item.unit} */}
                    <Segment as="a" basic style={{padding:0, width:'100%' ,margin:0, color:selected?color(props.item.tags):'white'}}>
                    {props.item.qty} <br/> {props.item.unit}
                    </Segment>
                </Table.Cell>
                <Table.Cell width={12}
                    style={{paddingLeft:5, lineHeight:1, whiteSpace:'preWrap'}}
                > 
                    <Segment as="a" basic style={{margin:0, padding:0, color:selected?color(props.item.tags):'white'}}>
                    {props.item.name} <strong >{props.item.marked ? " *" : ""}</strong>
                    <br/>
                    <i>{props.item.notes}</i>
                    </Segment>
                </Table.Cell>
            </Table.Row>
        )
    }
}

export default Item;




