import React, { Component } from 'react';
import {TransitionablePortal, Segment, Button, Header, Divider} from 'semantic-ui-react';
// import CategoryItems from './CategoryItems'

class SortPortal extends Component {

    render() {
        const sortButton = (sortMode, label = sortMode) => {
            return (
                <Button 
                    style={{opacity:(this.props.sortMode === sortMode)?1:0.5, textTransform:(this.props.sortMode === sortMode)?'uppercase':'none'}}  
                    onClick={this.props.sortModeSelected.bind(this,sortMode)}
                >
                    {label}
                </Button>
            )
        }
        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                >
                    <Segment inverted textAlign="center"
                        style={{ borderRadius:"0em", right: '0%', margin:0, paddingTop:7, paddingBottom:0, position: 'fixed', top: '54px', zIndex: 5004, width:"50%", height:"auto"}}
                    >
                        <Button.Group size="massive" compact className="black">
                            <Button icon="apple"
                                style={{opacity:(this.props.filter.name === 'Fruit')?1:0.5}}   
                                onClick={this.props.fruitSelected.bind(this, 'Fruit',"*")}
                            />
                            <Button icon="leaf"
                                style={{opacity:(this.props.filter.name === 'Veg')?1:0.5}}   
                                onClick={this.props.vegSelected.bind(this, 'Veg',"*")}
                            />
                        </Button.Group>
                        <p/>
                        <Header disabled dividing inverted size="tiny" content="SORTING"/>
                        <Button.Group vertical className="black">
                            {sortButton("Alphabetical")}
                            {sortButton("Grouped")}
                            {sortButton("Timestamp", "None")}
                        </Button.Group>
                        <Divider/>
                        <Button className="black" 
                            style={{
                                opacity:(this.props.reverse)?1:0.5,
                                textTransform:(this.props.reverse)?'uppercase':'none'
                            }}   
                            onClick={this.props.sortDirectionChanged}
                        >
                            {this.props.reverse?"Descending":"Ascending"}
                        </Button>
                        <Divider/>
                        <Segment inverted>
                            <Button inverted basic icon="check" size="massive"
                                style={{opacity:(this.props.filter.name === 'completed')?1:0.5}}                            
                                onClick={this.props.completedClicked}
                            />
                        </Segment>
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default SortPortal;



